import {connect} from "react-redux";
import VehicleDetails from "../../VehicleDetails/VehicleDetails"

function mapStateToProps(state) {
    console.log('vehicle details redux map state to props:', state);
    const index = state.mileagePage.vehicleDetails.vehicleIndex;
    let vehicleDetails;
    if (index == null)
        vehicleDetails = null;
    else
        vehicleDetails = state.mileagePage.vehicleData.car_details[index];

    return {
        car: vehicleDetails,
    };
}

export default connect(
    mapStateToProps,
    {}
)(VehicleDetails);
