export const SHOW_CAR_DETAILS = 'show_car_details';

const initialState = {
    vehicleIndex: null,
};

export function reducer(state = initialState, action) {
    switch (action.type) {
        case SHOW_CAR_DETAILS:
            return {
                ...state,
                vehicleIndex: action.data.vehicleIndex,
            };
        default:
            return state;
    }
}

export function showCarDetails(index = null) {
    return {
        type: SHOW_CAR_DETAILS,
        data: {
            vehicleIndex: index,
        }
    };
}
