import React, {Component} from 'react';
import {PropTypes} from 'prop-types';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as actions from '../../../redux/charts/fetchChartData';
import {showCarDetails} from '../../../redux/charts/vehicleDetailsReducer'
import CarsChart from '../../CarsChart/CarsChart';
import Timestamp from '../../Timestamp/Timestamp'

import './MileagePage.scss'

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import VehicleDetailsRedux from "../VehicleDetailsRedux/VehicleDetailsRedux";

export class MileagePage extends Component {
    static propTypes = {
        chartData: PropTypes.object.isRequired,
        showCarDetails: PropTypes.func,
    };

    render() {
        return (
            <Container>
                <Row>
                    <Col className="charts-mileage-page">
                        <CarsChart chartData={this.props.chartData}
                                   onCarClick={this.props.showCarDetails}/>
                    </Col>
                </Row>

                <Row>
                    <Col>
                        <VehicleDetailsRedux/>
                    </Col>
                </Row>

                <Row>
                    <Col>
                        <Timestamp timestamp={this.props.chartData.timestamp}/>
                    </Col>
                </Row>
            </Container>
        );
    }

    componentDidMount() {
        this.props.fetchChartData()
    }
}

function mapStateToProps(state) {
    return {
        chartData: state.mileagePage.vehicleData,
    };
}

/* istanbul ignore next */
function mapDispatchToProps(dispatch) {
    return {
        ...bindActionCreators({...actions, showCarDetails}, dispatch)
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(MileagePage);
