import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import './VehicleDetails.scss'
import Table from 'react-bootstrap/Table'
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

export default class VehicleDetails extends PureComponent {
    static propTypes = {
        car: PropTypes.object,
    };

    render() {
        let car = this.props.car;
        if (car === null || car === undefined) {
            return (<div className="vehicle-details">Brak danych</div>);
        }

        let imported_str;
        let imported = car.imported;
        if (imported === null)
            imported_str = 'Brak danych';
        else if (imported)
            imported_str = 'Tak';
        else
            imported_str = 'Nie';

        // const map_uri = 'https://www.google.com/maps/search/?api=1&query=' + escape(car.location);

        return (
            <Container>
                <Row>
                    <Col xs={{span: 0}}/>
                    <Col className="justify-content-md-center" xs={{span: 12}} xl={{span: 6, offset: 3}}>
                        <Card>
                            <Card.Header as='h5'>
                                <Container>
                                    <Row>
                                        <Col >
                                            {car.name}
                                        </Col>
                                        <Col xs='auto'>
                                            <Button variant="primary" href={car.link} size='sm'>Allegro</Button>
                                        </Col>
                                    </Row>
                                </Container>
                            </Card.Header>
                            <Card.Img variant="top" src={car.image}/>
                            <Card.Body>
                                <Card.Text>
                                    <Table size='sm'>
                                        <tr key='make' id='make'>
                                            <td key='key'>Marka</td>
                                            <td key='value'>{car.make}</td>
                                        </tr>
                                        <tr key='model' id='model'>
                                            <td key='key'>Model</td>
                                            <td key='value'>{car.model}</td>
                                        </tr>
                                        <tr key='price' id='price'>
                                            <td key='key'>Cena</td>
                                            <td key='value'>{car.price}</td>
                                        </tr>
                                        <tr key='year'>
                                            <td key='key'>Rocznik</td>
                                            <td key='value'>{car.year}</td>
                                        </tr>
                                        <tr key='mileage'>
                                            <td key='key'>Przebieg</td>
                                            <td key='value'>{car.mileage}</td>
                                        </tr>
                                        <tr key='location'>
                                            <td key='key'>Localizacja</td>
                                            <td key='value'>
                                                {/*<ahref={map_uri}>*/car.location/*</a>*/}
                                            </td>
                                        </tr>
                                        <tr key='imported'>
                                            <td key='key'>Importowany</td>
                                            <td key='value'>{imported_str}</td>
                                        </tr>
                                    </Table>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
        );
    }
}
