import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import Moment from "react-moment";
import 'moment/locale/pl';

import './Timestamp.scss'

export default class Timestamp extends PureComponent {
    static propTypes = {
        timestamp: PropTypes.number
    };

    render() {
        let ts_part = this.props.timestamp ?
            <Moment unix={true} fromNow locale='pl'>{this.props.timestamp}</Moment> : "Brak danych";
        return <div className="charts-timestamp">
            Ostatnia aktualizacja: {ts_part}
        </div>
    }
}
