import {CHARTS_FETCH_CHART_DATA_FAILURE, CHARTS_FETCH_CHART_DATA_SUCCESS} from "./fetchChartData";


const initialState = {
    series: [],
    avg_series: [],
    car_details: [],
    max_year: null,
    min_year: null,
    timestamp: null,
};

export function reducer(state = initialState, action) {
    switch (action.type) {
        case CHARTS_FETCH_CHART_DATA_SUCCESS:
            return {
                ...state,
                ...action.data.data,
            };
        case CHARTS_FETCH_CHART_DATA_FAILURE:
            return {
                ...state,
                ...initialState
            };
        default:
            return state;
    }
}
