export const CHARTS_FETCH_CHART_DATA_BEGIN = 'CHARTS_FETCH_CHART_DATA_BEGIN';
export const CHARTS_FETCH_CHART_DATA_SUCCESS = 'CHARTS_FETCH_CHART_DATA_SUCCESS';
export const CHARTS_FETCH_CHART_DATA_FAILURE = 'CHARTS_FETCH_CHART_DATA_FAILURE';
export const CHARTS_FETCH_CHART_DATA_DISMISS_ERROR = 'CHARTS_FETCH_CHART_DATA_DISMISS_ERROR';

function _fetchChartDataSuccess(response) {
    return {
        type: CHARTS_FETCH_CHART_DATA_SUCCESS,
        data: response,
    }
}

function _fetchChartDataFailure(err) {
    return {
        type: CHARTS_FETCH_CHART_DATA_FAILURE,
        data: {error: err},
    }
}

// Rekit uses redux-thunk for async actions by default: https://github.com/gaearon/redux-thunk
// If you prefer redux-saga, you can use rekit-plugin-redux-saga: https://github.com/supnate/rekit-plugin-redux-saga
export function fetchChartData() {
    return (dispatch) => { // optionally you can have getState as the second argument
        dispatch({
            type: CHARTS_FETCH_CHART_DATA_BEGIN,
        });

        fetch('https://carscanner.github.io/carscanner-data/export.json')
            .then(response => response.json())
            .then(response => {
                dispatch(_fetchChartDataSuccess(response))
            })
            .catch(error => {
                dispatch(_fetchChartDataFailure(error))
            });
    }
}

// Async action saves request error by default, this method is used to dismiss the error info.
// If you don't want errors to be saved in Redux store, just ignore this method.
export function dismissFetchChartDataError() {
    return {
        type: CHARTS_FETCH_CHART_DATA_DISMISS_ERROR,
    };
}

const initialState = {
    fetchChartDataPending: false,
    fetchChartDataError: null,
};

export function reducer(state = initialState, action) {
    switch (action.type) {
        case CHARTS_FETCH_CHART_DATA_BEGIN:
            // Just after a request is sent
            return {
                ...state,
                fetchChartDataPending: true,
                fetchChartDataError: null,
            };

        case CHARTS_FETCH_CHART_DATA_SUCCESS:
            // The request is success
            return {
                ...state,
                fetchChartDataPending: false,
                fetchChartDataError: null,
            };

        case CHARTS_FETCH_CHART_DATA_FAILURE:
            // The request is failed
            return {
                ...state,
                fetchChartDataPending: false,
                fetchChartDataError: action.data.error,
            };

        case CHARTS_FETCH_CHART_DATA_DISMISS_ERROR:
            // Dismiss the request failure error
            return {
                ...state,
                fetchChartDataError: null,
            };

        default:
            return state;
    }
}
