import {combineReducers} from 'redux';
import {reducer as fetchVehicleData} from "./charts/fetchChartData";
import {reducer as vehicleData} from './charts/vehicleDataReducer';
import {reducer as vehicleDetails} from './charts/vehicleDetailsReducer'

export default combineReducers({
    mileagePage: combineReducers({
        fetchVehicleData,
        vehicleData,
        vehicleDetails,
    }),
});
