import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import Chart from 'react-apexcharts'

export default class CarsChart extends PureComponent {
    static propTypes = {
        chartData: PropTypes.object,
        onCarClick: PropTypes.func,
    };

    constructor(props) {
        super(props);
        this._chartCarClick = this._chartCarClick.bind(this);
    }

    render() {
        let series = [
            {
                data: this.props.chartData.series,
                name: 'Pojazdy',
                type: 'scatter',
            },
            {
                data: this.props.chartData.avg_series,
                name: 'Średni',
                type: 'line'
            },
        ];

        let options = {
            chart: {
                events: {
                    dataPointSelection: this._chartCarClick,
                },
                height: 600,
                toolbar: {
                    tools: {
                        download: false,
                    }
                },
                zoom: {
                    enabled: true,
                    type: 'xy',
                    zoomedArea: {
                        fill: {
                            color: '#90CAF9',
                            opacity: 0.4
                        },
                        stroke: {
                            color: '#0D47A1',
                            opacity: 0.4,
                            width: 1
                        }
                    }
                },
            },
            noData: {
                text: 'Brak danych',
            },
            theme: {
                palette: 'palette6',
            },
            xaxis: {
                tickAmount: this.props.chartData.max_age,
                min: 0,
                max: this.props.chartData.max_age,
                tickPlacement: 'on',
                labels: {
                    formatter: this.xAxisLabelFormatter.bind(this),
                },
            },
            yaxis: {
                min: 0,
                title: {
                    text: 'Przebieg',
                },
            },
            tooltip: {
                // custom: this.displayTooltip.bind(this),
                enabled: false,
                // shared: false,
                // intersect: true,
            },
            fill: {
                type: 'solid',
            },
            markers: {
                size: [6, 0]
            },
            legend: {
                show: true
            },
        };

        return (
            <div className="charts-cars-chart">
                <Chart options={options} series={series} type="line" width={800} height={600}/>
            </div>
        );
    }

    _chartCarClick(event, chartContext, config) {
        this.props.onCarClick(config.dataPointIndex);
    }

    xAxisLabelFormatter(value, timestamp, index) {
        const d = this.props.chartData;
        const result = d.min_year + d.max_age - value;
        return result === Math.ceil(result) ? result : '';
    }
}

